<template>
	<div id="snack">
		<div id="info">
			<h1></h1>
			<p>WSAS</p>
		</div>
		<div id="snack_div">
			<table>
				<tr v-for="(col,y) in cols">
					<td v-for="(row,x) in rows" :class="snackbody(x,y)||showfood(x,y)?'active':''">
					</td>
				</tr>
			</table>
			<el-button :disabled="dis" class="el_btn_start" @click="start()" type="primary"></el-button>
			<el-button :disabled="dis_end"  class="el_btn_end" @click="end()" type="danger"></el-button>
		</div>
		<div class="score">
			<ul>
				<li></li>
				<li>{{score}}</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
			data:function(){
				return{
					timer:'',//
					rows:'',//
					cols:'',//
					position:[[0,0],[1,0],[2,0],[3,0]],//
					direction:1,//
					food:[],//
					dis:false,
					dis_end:true,
					score:-1,
				}
			},
			mounted(){
				this.keyboard()
			    this.background() 
			    this.creatfood()
			},
			methods:{
				background(){//
					this.rows=Array(40)
					this.cols=Array(20)
				},
				keyboard(){//
					let _this = this;
				    document.onkeydown = function(e){
				    	if(e.keyCode===65){//A
				    		_this.change(-1)
				    	}else if(e.keyCode===87){//W
				    		_this.change(-2)
				    	}else if(e.keyCode===68){//D
				    		_this.change(1)
				    	}else{//S
				    		_this.change(2)
				    	}
				    }
				},
				creatfood(){//
					this.food[0]=Math.floor(Math.random()*40)
					this.food[1]=Math.floor(Math.random()*20)
					this.score++
				},
				showfood(x,y){//
					if(this.food[0]===x&&this.food[1]===y){
						return true
					}
				},
				snackbody(x,y){//
					let X,Y
					for(let i=0;i<this.position.length;i++){
						if(this.position[i][0]===x&&this.position[i][1]===y){
							return true
						}
					}
				},	
				start(){//
					this.dis =true
					this.dis_end = false
					this.timer=setInterval(()=>this.autorun(),250)
				},	
				end(){
					this.$message({
			          message: '',
			          type: 'warning'
			        });
					clearInterval(this.timer)
					this.position=[[0,0],[1,0],[2,0],[3,0]]
					this.creatfood()
					this.direction=1
					this.dis =false
					this.dis_end = true
					this.score = 0
				},		
				change(dir){//
					if(Math.abs(dir)===Math.abs(this.direction)){//
							return
					}else{
						this.direction=dir//
					}
				},
				autorun(){					
					let direction=this.direction//
					let headX,headY
						headX=this.position[this.position.length-1][0]//X
						headY=this.position[this.position.length-1][1]//Y
					if(direction===1||direction===-1){//     						
							direction>0?headX++:headX--//X+1X-1 										
					}else{
							direction>0?headY++:headY--//Y
					}//[headX,headY]
					if(headX<0||headX>39||headY<0||headY>19){//
						this.$alert(''+'       '+this.score)
						clearInterval(this.timer)
						this.position=[[0,0],[1,0],[2,0],[3,0]]
						this.creatfood()
						this.direction=1
						this.dis =false
						this.dis_end = true
						this.score = 0
						console.log(headX+headY)
					}
					else{//						
						this.position.push([headX,headY])//
						if(headX!==this.food[0]||headY!==this.food[1]){	//							
							this.position.shift()//

						}else{//
							this.creatfood()//bug
						}
					}									
				}							
			}
	}
</script>

<style lang="scss" scoped>
		#snack{
			width:100%;
			background: #000;
			padding:100px 0 50px 0;
		}
		td{
			border: 1px solid #666;
			width: 20px !important;
			height: 20px !important;
		}
		table{
			border-collapse: collapse;
		}
		#snack_div{
			width:800px;
			margin:0 auto;
		}
		.active{
			background: #1e9259;
		}
		.el_btn_start{ 
			padding: 12px 40px;
			margin-top:20px;
		}
		.el_btn_end{
			padding: 12px 40px;
			margin-top:20px;
			margin-left: 100px;
		}
		#info{
			user-select:none;
			color:#8f959a;
			padding-bottom:20px;
			h1{
				display:inline-block;
				font-size:20px;
				margin-bottom:20px;
				border-bottom:1px solid #eee;
			}
		}
		.score{
			display:inline-block;
			width:60px;
			position:absolute;
			right:100px;
			top:300px;
			ul{
				li{
					color:#fff;
					font-weight:bold;
					line-height:40px;
				}
				li:first-child{
					font-size:20px;
				}
				li:last-child{
					font-size:24px;
				}
			}
		}
</style>





